*{
    -ms-overflow-style: none;
}
::-webkit-scrollbar {
    display: none;
}

@font-face {
  font-family: "Helvetica";
  src: url('../fonts/Helvetica.ttf');
}
.chat{
  height:60vh;
  background-color: #e4dcd4;

}

.knowbot{
  width:8%;
  height:8%;
  padding-top: 5%;
  margin-right: 2%;
  background-color: #e4dcd4;
}

.avatar{
  width:8%;
  height:8%;
  padding-top: 1%;
  background-color: #e4dcd4;
}

.chatcolor{
  background-color: #e4dcd4 !important;
}

.botmessage{
  background-color: #ffffff;
  vertical-align: middle;
  padding: 1vh;
  font-family: "Helvetica";
  border-radius: 20px;
  resize: both;
  width:80%;
}

.usermessage{
  background-color: #ddf9c6;
  width: 80%;
  margin-left: 10%;
  height: auto;
  vertical-align: middle;
  font-family: "Helvetica";
  border-radius: 20px;
  overflow: hidden;
  padding: 0.5vh;
}

.bot{
  background-color: #e4dcd4;

}

ul,li{
  list-style: none;
  background-color: #e4dcd4;
}

.ideabutton{
  width:7%;
  display: inline;
  position: relative;
  height: 100%;
  border: none;
  background-color: #e4dcd4;
  padding-right: 1vh;
  margin-left: 2vh;
}
.sendbutton{
  width:7%;
  display: inline;
  position: relative;
  height: 90%;
  border: none;
  background-color: #e4dcd4;
  margin-left: 2vw;
}

.skipbutton{
  text-align: center;
  color: #000000;
  font-family: "Helvetica";
  font-size:0.9em;
  height: auto;
  border-radius: 25px;
  border-style: solid;
  border-width: thin;
  border-color: 0;
  width:10%;
  display: inline;
  text-align: center;
  position: relative;
  background-color: #e4dcd4;
  margin-right: 1vw;
  margin-left: 1vw;
}

.input-area{
  bottom: 5%;
  height: 6vh;
  width: 100%;
  position: relative;
  padding-bottom: 1vh;
  background-color: #e4dcd4;
}

.chatarea{
  background-color: #e4dcd4;
  height: 70vh;
  overflow-y: auto;
  overflow-x: hidden;
}

.idealogo{
  height: auto;
  width: 100%;
  background-color: #e4dcd4;
}

.input-text{
  background-color: #ffffff;
  border-radius: 10px;
  position: relative;
  width: 70%;
  font-family: "Helvetica";
  padding-top: 1vh;
  padding-bottom: 1vh;
}

.scroll-div{
  height: 20vh;
  background-color: #e4dcd4;
}

@media (min-width: 768px){
  .knowbot{
    width:8%;
    height:1%;
    padding: 1vh;
    background-color: #e4dcd4;
  }
  .botmessage{
    background-color: #ffffff;
    width: 70%;
    margin: 1vh;
    vertical-align: middle;
    padding: 1vh;
    font-family: "Helvetica";
    border-radius: 20px;
    resize: both;
  }
  .usermessage{
    background-color: #ddf9c6;
    width: 70%;
    margin-left: 20%;
    height: auto;
    vertical-align: middle;
    font-family: "Helvetica";
    border-radius: 20px;
    overflow: hidden;
    padding: 0.5vh;
    padding-left: 1vw;
  }
  .avatar{
    width:8%;
    height:auto;
    background-color: #e4dcd4;
  }
  .ideabutton{
    width:4%;
    padding: 1vh;
    background-color: #e4dcd4 !important;
    border-radius: 20px;
    border-color: #000000;
    border-width: thin;
  }
  .sendbutton{
    width:7%;
    padding: 1vh;
    background-color: #e4dcd4 !important;
    border-radius: 20px;
    border-color: #000000;
    border-width: thin;
    margin-left: 1vh;
  }
  .skipbutton{
    width:10%;
    display: inline;
    text-align: center;
    position: relative;
    height: 100%;
    border: none;
    background-color: #e4dcd4 !important;
    color: #000000;
    font-weight: bold;
    font-family: "Helvetica";
    font-size:1.1em;
    margin-right: 1vh;
    border-radius: 20px;
  }

  .input-area{
    bottom: 5%;
    height: 6vh;
    width: 100%;
    position: relative;
    padding-bottom: 1vh;
    background-color: #e4dcd4;
  }

  .input-text{
    background-color: #ffffff;
    border-radius: 10px;
    position: relative;
    width: 70%;
    font-family: "Helvetica";
    padding-top: 1vh;
    padding-bottom: 1vh;
  }
  .chatarea{
    background-color: #e4dcd4;
    height: 75vh;
    overflow-y: auto;
    overflow-x: hidden;
  }
}
