* {
  box-sizing: border-box;
  background-color: #edeff2;
}

@font-face {
  font-family: "Helvetica";
  src: url('../fonts/Helvetica.ttf');
}

.login-logo{
  height: 150px;
  background-color: #ffffff;
  padding: 20px;
}

.window-color{
  background-color: #ffffff;
}

.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.title {
  font-family: 'Helvetica';
  font-size:1.3em;
  font-weight: bolder;
  color: 0;
  text-align: center;
  background-color: #ffffff;
  padding-bottom: 3vh;
}

.logintab{
  padding: 2vh;
}

.white{
  background-color: white;
}

.signin{
  font-size:1.2em;
  color: #ffffff;
  background-color: #959595;
  display: block;
  width: 70%;
  text-align: center;
  margin-left: 15%;
  margin-top: 5%;
  height: auto;
  bottom: 10%;
  font-family: "Helvetica";

}
.pwdbtn{
  font-size:1.2em;
  color: #ffffff;
  background-color: #959595;
  display: block;
  width: 60%;
  text-align: center;
  margin-left: 20%;
  margin-top: 5%;
  height: auto;
  bottom: 10%;
  font-family: "Helvetica";
}
.text{
  background-color: #edeff2;
  width: 60%;
  margin-top: 6vh;
  margin-left: 20%;
  border: 0px;
  border-radius: 5px;
  color: #000000;
  font-size: 1.1em;
  font-family: "Helvetica";
}

.loginform{
  padding-top: 2vh;
  background-color: #ffffff;
}

.reset{
  color: #000000;
  background-color: #ffffff;
  text-align:center;
  font-size: 1.1em;
  font-family: "Helvetica";
}

.link{
  display: block;
  text-align: center;
  padding-top: 5vh;
}

label{
  color: #959595;
  background-color: #ffffff;
  font-family: "Helvetica";
  margin-left: 20%;
}

.pwdreset{
  height: 75vh;
  background-color: #ffffff;
  margin: 4vh;
}
.pwdtext{
  font-family: "Helvetica";
  font-size: 1.2em;
  padding-top: 2vh;
}

.pwdmessage{
  height: 15vh;
  font-family: "Helvetica";
  font-size: 1.1em;
  padding-top: 7vh;
}
